r\n  color: rgba(255, 255, 255, 0.8);\r\n  margin-bottom: 16px;\r\n  \r\n  i {\r\n    color: #667eea;\r\n  }\r\n  \r\n  .skill-count {\r\n    font-size: 12px;\r\n    font-weight: normal;\r\n    color: rgba(255, 255, 255, 0.4);\r\n  }\r\n}\r\n\r\n.empty-state {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  padding: 40px 20px;\r\n  background: rgba(255, 255, 255, 0.02);\r\n  border-radius: 16px;\r\n  border: 1px dashed rgba(255, 255, 255, 0.1);\r\n  \r\n  i {\r\n    font-size: 36px;\r\n    color: rgba(255, 255, 255, 0.2);\r\n    margin-bottom: 12px;\r\n  }\r\n  \r\n  p {\r\n    font-size: 14px;\r\n    color: rgba(255, 255, 255, 0.4);\r\n    margin: 0;\r\n  }\r\n  \r\n  .empty-hint {\r\n    font-size: 12px;\r\n    color: rgba(255, 255, 255, 0.25);\r\n    margin-top: 6px;\r\n  }\r\n}\r\n\r\n.skill-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 14px;\r\n}\r\n\r\n.skill-card {\r\n  padding: 16px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 14px;\r\n  border: 1px solid rgba(255, 255, 255, 0.08);\r\n  transition: all 0.2s;\r\n  \r\n  &:hover {\r\n    background: rgba(255, 255, 255, 0.05);\r\n    transform: translateY(-2px);\r\n    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);\r\n  }\r\n  \r\n  &.rarity-c { border-left: 3px solid #9ca3af; }\r\n  &.rarity-b { border-left: 3px solid #60a5fa; }\r\n  &.rarity-a { border-left: 3px solid #a78bfa; }\r\n  &.rarity-s { border-left: 3px solid #fbbf24; }\r\n  &.rarity-ss { border-left: 3px solid #f472b6; }\r\n}\r\n\r\n.skill-header-row {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.skill-name-area {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n}\r\n\r\n.skill-name {\r\n  font-size: 16px;\r\n  font-weight: 700;\r\n  color: white;\r\n}\r\n\r\n.skill-rarity {\r\n  font-size: 10px;\r\n  font-weight: 600;\r\n  padding: 2px 6px;\r\n  border-radius: 4px;\r\n  \r\n  &.rarity-c { background: rgba(156, 163, 175, 0.3); color: #d1d5db; }\r\n  &.rarity-b { background: rgba(96, 165, 250, 0.3); color: #93c5fd; }\r\n  &.rarity-a { background: rgba(167, 139, 250, 0.3); color: #c4b5fd; }\r\n  &.rarity-s { background: rgba(251, 191, 36, 0.3); color: #fcd34d; }\r\n  &.rarity-ss { background: rgba(244, 114, 182, 0.3); color: #f9a8d4; }\r\n}\r\n\r\n.skill-level {\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  color: #667eea;\r\n  background: rgba(102, 126, 234, 0.2);\r\n  padding: 4px 10px;\r\n  border-radius: 8px;\r\n}\r\n\r\n.skill-description {\r\n  font-size: 13px;\r\n  color: rgba(255, 255, 255, 0.6);\r\n  line-height: 1.5;\r\n  margin: 0 0 12px 0;\r\n}\r\n\r\n.skill-stats {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 8px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.stat-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 4px;\r\n  font-size: 11px;\r\n  padding: 4px 8px;\r\n  border-radius: 6px;\r\n  \r\n  i { font-size: 10px; }\r\n  \r\n  &.cost {\r\n    background: rgba(52, 211, 153, 0.2);\r\n    color: #6ee7b7;\r\n    border: 1px solid rgba(52, 211, 153, 0.3);\r\n  }\r\n  \r\n  &.cooldown {\r\n    background: rgba(96, 165, 250, 0.2);\r\n    color: #93c5fd;\r\n    border: 1px solid rgba(96, 165, 250, 0.3);\r\n  }\r\n  \r\n  &.accuracy {\r\n    background: rgba(251, 191, 36, 0.2);\r\n    color: #fcd34d;\r\n    border: 1px solid rgba(251, 191, 36, 0.3);\r\n  }\r\n}\r\n\r\n.skill-damage {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n  font-size: 12px;\r\n  margin-bottom: 10px;\r\n  padding: 8px 10px;\r\n  background: rgba(239, 68, 68, 0.1);\r\n  border-radius: 8px;\r\n  \r\n  .damage-label {\r\n    color: rgba(255, 255, 255, 0.5);\r\n  }\r\n  \r\n  .damage-source {\r\n    color: #f87171;\r\n    font-weight: 500;\r\n  }\r\n  \r\n  .damage-value {\r\n    color: #fcd34d;\r\n    font-weight: 600;\r\n  }\r\n}\r\n\r\n.skill-mechanics {\r\n  display: flex;\r\n  gap: 8px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.mechanic-tag {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 4px;\r\n  font-size: 10px;\r\n  padding: 3px 8px;\r\n  border-radius: 4px;\r\n  \r\n  i { font-size: 9px; }\r\n  \r\n  &.ignore-def {\r\n    background: rgba(239, 68, 68, 0.2);\r\n    color: #f87171;\r\n    border: 1px solid rgba(239, 68, 68, 0.3);\r\n  }\r\n  \r\n  &.no-dodge {\r\n    background: rgba(167, 139, 250, 0.2);\r\n    color: #c4b5fd;\r\n    border: 1px solid rgba(167, 139, 250, 0.3);\r\n  }\r\n}\r\n\r\n.skill-upgrade {\r\n  display: flex;\r\n  justify-content: flex-end;\r\n  padding-top: 10px;\r\n  border-top: 1px solid rgba(255, 255, 255, 0.05);\r\n}\r\n\r\n.upgrade-btn {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n  padding: 8px 16px;\r\n  background: linear-gradient(135deg, #667eea, #764ba2);\r\n  border: none;\r\n  border-radius: 8px;\r\n  color: white;\r\n  font-size: 12px;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n  \r\n  &:hover:not(:disabled) {\r\n    transform: scale(1.05);\r\n    box-shadow: 0 4px 16px rgba(102, 126, 234, 0.4);\r\n  }\r\n  \r\n  &:disabled {\r\n    opacity: 0.5;\r\n    cursor: not-allowed;\r\n  }\r\n}\r\n\r\n.skill-effects-section {\r\n  margin-top: 20px;\r\n}\r\n\r\n.effects-grid {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 12px;\r\n}\r\n\r\n.effect-card {\r\n  padding: 12px;\r\n  background: rgba(255, 255, 255, 0.02);\r\n  border-radius: 10px;\r\n  border: 1px solid rgba(255, 255, 255, 0.05);\r\n}\r\n\r\n.effect-header {\r\n  font-size: 12px;\r\n  font-weight: 600;\r\n  color: #667eea;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.effect-list {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 8px;\r\n}\r\n\r\n.effect-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 4px;\r\n  font-size: 11px;\r\n  padding: 4px 8px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 6px;\r\n  \r\n  .effect-type {\r\n    color: rgba(255, 255, 255, 0.6);\r\n  }\r\n  \r\n  .effect-value {\r\n    font-weight: 600;\r\n    \r\n    &.positive { color: #34d399; }\r\n    &.negative { color: #f87171; }\r\n  }\r\n  \r\n  .effect-duration {\r\n    color: rgba(255, 255, 255, 0.4);\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
