resent-list {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 10px;\r\n}\r\n\r\n.present-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n  padding: 10px 14px;\r\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.15), rgba(102, 126, 234, 0.05));\r\n  border: 1px solid rgba(102, 126, 234, 0.2);\r\n  border-radius: 12px;\r\n  \r\n  .present-avatar {\r\n    width: 32px;\r\n    height: 32px;\r\n    border-radius: 50%;\r\n    background: rgba(102, 126, 234, 0.3);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    \r\n    i {\r\n      font-size: 14px;\r\n      color: #a5b4fc;\r\n    }\r\n  }\r\n  \r\n  .present-name {\r\n    font-size: 13px;\r\n    font-weight: 500;\r\n    color: white;\r\n  }\r\n}\r\n\r\n.relationship-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 12px;\r\n}\r\n\r\n.relationship-card {\r\n  padding: 16px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 16px;\r\n  border: 1px solid rgba(255, 255, 255, 0.08);\r\n}\r\n\r\n.rel-header {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 14px;\r\n  margin-bottom: 16px;\r\n}\r\n\r\n.rel-avatar {\r\n  width: 48px;\r\n  height: 48px;\r\n  border-radius: 50%;\r\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.2));\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  \r\n  i {\r\n    font-size: 20px;\r\n    color: rgba(255, 255, 255, 0.6);\r\n  }\r\n}\r\n\r\n.rel-info {\r\n  flex: 1;\r\n}\r\n\r\n.rel-name {\r\n  font-size: 16px;\r\n  font-weight: 600;\r\n  color: white;\r\n  margin-bottom: 6px;\r\n}\r\n\r\n.rel-type {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n  padding: 4px 10px;\r\n  border-radius: 12px;\r\n  font-size: 11px;\r\n  font-weight: 500;\r\n  \r\n  i {\r\n    font-size: 10px;\r\n  }\r\n  \r\n  &.type-stranger {\r\n    background: rgba(156, 163, 175, 0.15);\r\n    color: #d1d5db;\r\n  }\r\n  \r\n  &.type-classmate {\r\n    background: rgba(96, 165, 250, 0.15);\r\n    color: #93c5fd;\r\n  }\r\n  \r\n  &.type-friend {\r\n    background: rgba(52, 211, 153, 0.15);\r\n    color: #6ee7b7;\r\n  }\r\n  \r\n  &.type-lover {\r\n    background: rgba(244, 114, 182, 0.15);\r\n    color: #f9a8d4;\r\n  }\r\n  \r\n  &.type-master {\r\n    background: rgba(251, 191, 36, 0.15);\r\n    color: #fcd34d;\r\n  }\r\n  \r\n  &.type-submissive {\r\n    background: rgba(167, 139, 250, 0.15);\r\n    color: #c4b5fd;\r\n  }\r\n  \r\n  &.type-enemy {\r\n    background: rgba(248, 113, 113, 0.15);\r\n    color: #fca5a5;\r\n  }\r\n  \r\n  &.type-unknown {\r\n    background: rgba(156, 163, 175, 0.1);\r\n    color: rgba(255, 255, 255, 0.4);\r\n  }\r\n}\r\n\r\n.rel-stats {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 12px;\r\n}\r\n\r\n.stat-item {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 6px;\r\n}\r\n\r\n.stat-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.stat-label {\r\n  font-size: 11px;\r\n  color: rgba(255, 255, 255, 0.5);\r\n}\r\n\r\n.stat-value {\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  font-family: 'JetBrains Mono', monospace;\r\n  \r\n  &.very-high { color: #f472b6; }\r\n  &.high { color: #34d399; }\r\n  &.medium { color: #60a5fa; }\r\n  &.low { color: #fbbf24; }\r\n  &.very-low { color: rgba(255, 255, 255, 0.4); }\r\n  &.training { color: #a78bfa; }\r\n}\r\n\r\n.stat-bar {\r\n  height: 6px;\r\n  background: rgba(255, 255, 255, 0.1);\r\n  border-radius: 3px;\r\n  overflow: hidden;\r\n}\r\n\r\n.stat-fill {\r\n  height: 100%;\r\n  border-radius: 3px;\r\n  transition: width 0.4s ease;\r\n  \r\n  &.affection {\r\n    &.very-high { background: linear-gradient(90deg, #ec4899, #f472b6); }\r\n    &.high { background: linear-gradient(90deg, #10b981, #34d399); }\r\n    &.medium { background: linear-gradient(90deg, #3b82f6, #60a5fa); }\r\n    &.low { background: linear-gradient(90deg, #f59e0b, #fbbf24); }\r\n    &.very-low { background: rgba(255, 255, 255, 0.2); }\r\n  }\r\n  \r\n  &.training {\r\n    background: linear-gradient(90deg, #8b5cf6, #a78bfa);\r\n  }\r\n  \r\n  &.submission {\r\n    &.very-high { background: linear-gradient(90deg, #ec4899, #f472b6); }\r\n    &.high { background: linear-gradient(90deg, #f59e0b, #fbbf24); }\r\n    &.medium { background: linear-gradient(90deg, #3b82f6, #60a5fa); }\r\n    &.low { background: linear-gradient(90deg, #10b981, #34d399); }\r\n    &.very-low { background: rgba(255, 255, 255, 0.2); }\r\n  }\r\n}\r\n\r\n.empty-state {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  padding: 60px 20px;\r\n  text-align: center;\r\n  \r\n  .empty-icon {\r\n    width: 80px;\r\n    height: 80px;\r\n    border-radius: 50%;\r\n    background: rgba(255, 255, 255, 0.03);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-bottom: 20px;\r\n    \r\n    i {\r\n      font-size: 32px;\r\n      color: rgba(255, 255, 255, 0.15);\r\n    }\r\n  }\r\n  \r\n  .empty-title {\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    color: rgba(255, 255, 255, 0.5);\r\n    margin-bottom: 6px;\r\n  }\r\n  \r\n  .empty-desc {\r\n    font-size: 13px;\r\n    color: rgba(255, 255, 255, 0.3);\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
